

.intro {
  background-color: $white;
  color: $text-dark;
  text-align: center;
  padding: em(100px) 5%;
  & p {
    @extend %centered ;
    max-width: 600px;
  }
  & h2 {
    margin-top: 0;
  }
  @include mq(medium) {
    p {
      width: 60%;
    }
  }
}

.testimonial {

  background-color: lighten($dark-background, 70%);
  padding: em(50px) 5%;
  text-align: center;

    .testimonial__inner {
        .company {
          background-size: contain;
          height: 50px;
          width: 19%;
          display: inline-block;

          &--guavapass{
            @include image-bg('GuavaPass.png', "no");
            background-size: 75%;
            @extend .company;
          }
          &--melewi{
            @include image-bg('melewi.png', "no");
            background-size: contain;
            @extend .company;
          }
          &--on-two-planks{
            @include image-bg('OnTwoPlanks.png', "no");
            background-size: contain;
            @extend .company;
          }
          &--the-design-nomad{
            @include image-bg('the-design-nomad.png', "no");
            background-size: contain;
            @extend .company;
          }
          &--hello-haus{
            @include image-bg('hello-haus.png', "no");
            background-size: contain;
            @extend .company;
          }
      }
    }
}
